import AccountCircleRoundedIcon from '@mui/icons-material/AccountCircleRounded';
import ChatRoundedIcon from '@mui/icons-material/ChatRounded';
import {connect} from 'react-redux'
import {Navigate,useParams} from 'react-router-dom'
import {useState , useEffect,useCallback} from 'react'
import axios from 'axios'
import Cookies from 'js-cookie'

const Profile=({isAuthenticated,username})=>{
    const [USER,SETUSER]=useState({})
    const [follow , setFollow]=useState({})
    const [isLoading , setIsLoading]=useState(true)
    const {user_username}=useParams()

    // fetching another users profile
    const fetchUserOther=useCallback(async()=>{
        const config ={
            headers:{
                "Content-type":"application/json",
                "Accept":"application/json",
                'x-CSRFToken':Cookies.get('csrftoken')
            }
        }
        const body = JSON.stringify({user_username})
        try{
            const res = await axios.post(`${process.env.REACT_APP_API_URL}/profile/get_user/`,body,config)
            const data = await res.data
            SETUSER(data)
        }
        catch(err){
            const err_res={
                username:"User does not exits"
            }
            SETUSER(err_res)
        }
    },[user_username])

    //fetches data for numbers of followers and following
    const fetchUserFollow=useCallback(async()=>{
        const config = {
            headers:{
                "Content-type":"application/json",
                "Accept":"application/json",
                "x-CSRFToken":Cookies.get('csrftoken')
            }
        }
        const body = JSON.stringify({user_username})
        try{
            const res = await axios.post(`${process.env.REACT_APP_API_URL}/accounts/count_follow/`,body , config)
            const data = await res.data
            setFollow(data)
        }catch(err){
            return
        }
    },[user_username])

    useEffect(()=>{
        (async ()=>{
            if(user_username!=="me"){
                await fetchUserOther()
            }
            await fetchUserFollow()
            setIsLoading(false)
        })()
    },[user_username,fetchUserFollow, fetchUserOther])


    if(!isAuthenticated){
        return <Navigate to='/login'/>
    }
    return (
        <section className='profile'>
            <div className='profile__iconContainer'>
                <AccountCircleRoundedIcon/>
                {!isLoading &&   (user_username==="me" && <h1>{username}</h1>)}
                {!isLoading && (user_username!=="me" && <h1>{USER?.username}</h1>)}
                {isLoading && <h1 className='small_text bold_text center'>Loading</h1>}
            </div>
            <section className='profile__boxContainer'>
                <div className='profile__box'>
                    <p>Followers</p>
                    {!isLoading && <p>{follow?.followers}</p>}
                    {isLoading && <small className='small_text bold_text'>Loading</small>}
                </div>
                <div className='profile__box'>
                    <p>Following</p>
                    {!isLoading && <p>{follow?.following}</p>}
                    {isLoading && <small className='small_text bold_text'>Loading</small>}
                </div>
            </section>
            <section className='profile__footer'>
                    <button><ChatRoundedIcon/></button>
                    {user_username ==='me' && <button>Update Profile</button>}
                    {user_username !=='me' && <button>Follow</button>}
            </section>
        </section>
    )
}

const mapStateToProps=state=>{
    return {
        isAuthenticated:state.auth.isAuthenticated,
        username:state.profile.username
    }
}
export default connect(mapStateToProps)(Profile)