import {connect} from 'react-redux'

const Message=({sender , description, userid})=>{
    return (
        <>
        {
            sender === parseInt(userid) ?  (<div className='chat__msg msg__right'>{description}</div>) :  (<div className='chat__msg msg__left'>{description}</div>)
        }
        </>
    )
}

const mapStateToProps=state=>{
    return {
        userid:state.profile.userid
    }
}

export default connect(mapStateToProps)(Message)